
import { defineComponent, ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";

import { Pane } from "element-plus/lib/el-tabs/src/tabs.vue";
import { getBrandOptions } from "@/apis/brand";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "KYC",
  components: {},
  setup() {
    const router = useRouter();
    const route = useRoute();
    const tabValue = ref("");
    const searchValue = ref("adidas");
    const searchOptions = ref<OptionItem[]>([]);

    const onTabClick = async (pane: Pane) => {
      await router.push({ name: pane.props.name });
    };

    const init = async () => {
      const { data } = await getBrandOptions();
      searchOptions.value = data.brands.map((brand: string): OptionItem => ({label: brand, value: brand, checked: false}));
    };

    onMounted(() => {
      if (route.name) {
        tabValue.value = route.name as string;
      }
      init();
    });

    return {
      tabValue,
      searchValue,
      searchOptions,

      onTabClick
    };
  }
});

export default Component;
