<template>
  <div id="newsfeed-component">
    <el-alert
      title="Newsfeed from your favorite brands"
      type="info"
      effect="dark"
      show-icon
    >
    </el-alert>

    <el-row v-if="newsData.length" :gutter="30">
      <el-col :span="16">
        <NewsfeedCard description :data="newsData[0]"></NewsfeedCard>
      </el-col>
      <el-col :span="8">
        <el-row :style="{ padding: 0 }">
          <el-col v-if="newsData[1]" :span="24">
            <NewsfeedCard :data="newsData[1]"></NewsfeedCard>
          </el-col>
          <el-col v-if="newsData[2]" :span="24">
            <NewsfeedCard :data="newsData[2]"></NewsfeedCard>
          </el-col>
        </el-row>
      </el-col>

      <el-col
        v-for="(news, index) in _.splitAt(3, newsData)[1]"
        :key="index"
        :span="8"
      >
        <NewsfeedCard :data="news"></NewsfeedCard>
      </el-col>
    </el-row>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, toRefs, watchEffect } from "vue";
import * as _ from "ramda";

import NewsfeedCard from "@/components/NewsfeedCard.vue";
import { getNews } from "@/apis/kyc";
import { News } from "@/typings/kyc";

const Component = defineComponent({
  name: "Newsfeed",
  components: {
    NewsfeedCard
  },
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const { brand } = toRefs(props);
    const newsData = ref<News[]>([]);

    watchEffect(async () => {
      if (!brand.value) return;

      const { data } = await getNews(brand.value);
      newsData.value = data.news;
    });

    return {
      newsData,

      _
    };
  }
});

export default Component;
</script>

<style scoped lang="stylus">
#newsfeed-component
  overflow-x hidden
  padding-top 15px

  :deep() .el-row
    padding 20px 0

  :deep() .el-col
    margin-bottom 30px
</style>
