import { AxiosPromise } from "axios";
import request from "@/apis/http";
import { IgInfoResponse, IgNewsResponse, IgPostsResponse } from "@/typings/kyc";

export function getInstagramPosts(entry: string, brand: string): AxiosPromise<IgPostsResponse> {
  return request({
    url: `/kyc/social_media/instagram/posts/${brand}/${entry}/`,
    method: "GET",
  });
}

export function getInstagramInfo(brand: string): AxiosPromise<IgInfoResponse> {
  return request({
    url: `/kyc/social_media/instagram/info/${brand}/`,
    method: "GET",
  });
}

export function getNews(brand: string): AxiosPromise<IgNewsResponse> {
  return request({
    url: `/kyc/news/${brand}/`,
    method: "GET",
  });
}

