import { AxiosPromise } from "axios";
import request from "@/apis/http";
import {
  BoxPlotChartResponse, BrandSummaryRespones,
  BrandTableItemResponse,
  DiscoverOptionsResponse,
  FinanceRegionChartResponse,
  GrowthChartResponse,
  LineBarChartResponse,
  LineChartResponse,
  OverallChartResponse,
  PieChartResponse,
  RadarChartResponse,
  StackBarChartResponse,
  TableResponse
} from "@/typings/brandInsight";

export function getBrandOptions(): AxiosPromise<{brands: string[]}> {
  return request({
    url: '/brand_insight/brands/',
    method: 'GET'
  })
}

// *** For Brand Summary
export function getBrandSummary(brand: string): AxiosPromise<BrandSummaryRespones> {
  return request({
    url: `/brand_insight/brand_summary/${brand}/`,
    method: 'GET'
  })
}

