export function getBrandsTable(params: {'is_favorite'?: boolean; tags?: string;}): AxiosPromise<BrandTableItemResponse[]> {
  return request({
    url: `/discover_brands/brands_table/`,
    method: "GET",
    params
  });
}

export function getBrandMostViewed(): AxiosPromise<BrandTableItemResponse[]> {
  return request({
    url: `/my_favorite/most_viewd_brands/`,
    method: 'GET'
  })
}

export function putBrandFavorite(id: number, isFavorite: boolean) {
  return request({
    url: `/discover_brands/brand/${id}/`,
    method: 'PUT',
    data: {is_favorite: isFavorite}
  })
}

export function getDiscoverBrandOptions(): AxiosPromise<DiscoverOptionsResponse> {
  return request({
    url: '/discover_brands/tag_filter_options/',
    method: 'GET'
  })
}
