
import { defineComponent, toRefs, PropType } from "vue";
import dayjs from 'dayjs';
import { IgPost } from "@/typings/kyc";

const Component = defineComponent({
  name: "InstagramCard",
  props: {
    data: {
      type: Object as PropType<IgPost>,
      default: null
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const onCardClick = () => {
      window.open(data.value.url, "blank");
    };

    return {
      onCardClick,

      dayjs
    };
  }
});

export default Component;
