<template>
  <div id="instagram-card-component" @click="onCardClick">
    <div class="cover-container">
      <img class="cover" :src="data.stored_image_urls[0]" alt="" />
      <!--<div class="media-icon"></div>-->
    </div>
    <div class="description-container">
      <p>{{ data.content }}</p>
    </div>
    <div class="info-container">
      <div class="info-item">
        <font-awesome-icon
          class="icon"
          :icon="['far', 'heart']"
        ></font-awesome-icon>
        {{ data.likes }}
      </div>
      <div class="info-item">
        <font-awesome-icon
          class="icon"
          :icon="['far', 'comment']"
        ></font-awesome-icon>
        {{ data.comments_count }}
      </div>
      <div class="info-item">
        {{ dayjs(data.post_date).format('YYYY-MM-DD') }}
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import dayjs from 'dayjs';
import { IgPost } from "@/typings/kyc";

const Component = defineComponent({
  name: "InstagramCard",
  props: {
    data: {
      type: Object as PropType<IgPost>,
      default: null
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const onCardClick = () => {
      window.open(data.value.url, "blank");
    };

    return {
      onCardClick,

      dayjs
    };
  }
});

export default Component;
</script>

