<template>
  <div id="social-media-component">
    <el-row v-if="infoData" :gutter="40" class="social-media-card-zoom-container">
      <el-col :span="8">
        <SocialMediaCard
          :type="SOCIAL_MEDIA.INSTAGRAM"
          :data="infoData"
          @click="onCardClick('official')"
        ></SocialMediaCard>
      </el-col>
      <el-col :span="8">
        <SocialMediaCard
          :type="SOCIAL_MEDIA.FACEBOOK"
          :data="infoData"
          @click="onCardClick('tagtop')"
        ></SocialMediaCard>
      </el-col>
      <!--<el-col :span="6">-->
      <!--<SocialMediaCard :type="SOCIAL_MEDIA.TWEETER"></SocialMediaCard>-->
      <!--</el-col>-->
      <!--<el-col :span="6">-->
      <!--<SocialMediaCard :type="SOCIAL_MEDIA.LINKEDIN"></SocialMediaCard>-->
      <!--</el-col>-->
    </el-row>
    <div class="instagram-zoom-container">
      <div class="title-container">
        <font-awesome-icon
          class="logo"
          :icon="['fab', 'instagram']"
        ></font-awesome-icon>
        <h2 class="zoom-title">Instagram</h2>
      </div>
      <div class="cards-container">
        <el-row :gutter="40">
          <el-col v-for="(post, index) in postsData" :key="index" :span="8">
            <InstagramCard :data="post"></InstagramCard>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, watchEffect, toRefs } from "vue";

import SocialMediaCard from "@/components/SocialMediaCard.vue";
import InstagramCard from "@/components/InstagramCard.vue";

import { SOCIAL_MEDIA } from "@/constants/kyc";
import { getInstagramInfo, getInstagramPosts } from "@/apis/kyc";
import { IgInfoResponse, IgPost } from "@/typings/kyc";

const Component = defineComponent({
  name: "SocialMedia",
  components: {
    SocialMediaCard,
    InstagramCard
  },
  props: {
    brand: {
      type: String,
      default: ""
    }
  },
  setup(props) {
    const { brand } = toRefs(props);
    const infoData = ref<IgInfoResponse | null>(null);
    const postsData = ref<IgPost[]>([]);
    const postType = ref("official");

    watchEffect(async () => {
      if (!brand.value) return;

      const { data } = await getInstagramPosts(postType.value, brand.value);
      postsData.value = data.posts;
    });

    watchEffect(async () => {
      if (!brand.value) return;

      const { data } = await getInstagramInfo(brand.value);
      infoData.value = data;
    });

    const onCardClick = (type: string) => {
      postType.value = type;
    };

    return {
      infoData,
      postsData,

      onCardClick,

      SOCIAL_MEDIA
    };
  }
});

export default Component;
</script>

