<template>
  <div id="kyc-component">
    <div class="search-bar-container">
      <el-select v-model="searchValue" filterable placeholder="Search">
        <el-option
          v-for="item in searchOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
    </div>

    <div class="tabs-container">
      <el-tabs class="tabs" v-model="tabValue" @tab-click="onTabClick">
        <el-tab-pane label="Newsfeed" name="Newsfeed"></el-tab-pane>
        <el-tab-pane label="Social Media" name="SocialMedia"></el-tab-pane>
      </el-tabs>
    </div>

    <div class="child-router-container">
      <router-view :brand="searchValue"></router-view>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, onMounted } from "vue";
import { useRoute, useRouter } from "vue-router";

import { Pane } from "element-plus/lib/el-tabs/src/tabs.vue";
import { getBrandOptions } from "@/apis/brand";
import { OptionItem } from "@/typings/asideFilter";

const Component = defineComponent({
  name: "KYC",
  components: {},
  setup() {
    const router = useRouter();
    const route = useRoute();
    const tabValue = ref("");
    const searchValue = ref("adidas");
    const searchOptions = ref<OptionItem[]>([]);

    const onTabClick = async (pane: Pane) => {
      await router.push({ name: pane.props.name });
    };

    const init = async () => {
      const { data } = await getBrandOptions();
      searchOptions.value = data.brands.map((brand: string): OptionItem => ({label: brand, value: brand, checked: false}));
    };

    onMounted(() => {
      if (route.name) {
        tabValue.value = route.name as string;
      }
      init();
    });

    return {
      tabValue,
      searchValue,
      searchOptions,

      onTabClick
    };
  }
});

export default Component;
</script>

