<template>
  <div id="newsfeed-card-component" @click="onCardClick">
    <img class="cover" :src="data.image_url" alt="" />
    <div class="content-container">
      <h2 class="title" :class="{ description }">
        {{ data.title }}
      </h2>
      <div class="author-container">
        <p>Author:</p>
        <div class="name">
          {{ data.author }}
        </div>
      </div>
      <div v-if="description" class="description-container">
        <p>
          {{ data.content }}
        </p>
      </div>
      <div class="create-container">
        <p>Create At:</p>
        <div class="time">
          {{ dayjs(data.published_date).format("YYYY/MM/DD HH:mm") }}
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import dayjs from "dayjs";
import { News } from "@/typings/kyc";

const Component = defineComponent({
  name: "NewsfeedCard",
  props: {
    data: {
      type: Object as PropType<News>,
      default: null
    },
    description: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const { data } = toRefs(props);

    const onCardClick = async () => {
      await window.open(data.value.url, "blank");
    };

    return {
      onCardClick,

      dayjs
    };
  }
});

export default Component;
</script>

