<template>
  <div id="social-media-card-component" :class="socialMediaClass">
    <div class="avatar-container">
      <div class="avatar">
        <font-awesome-icon
          v-if="!data.image_url"
          class="icon"
          :icon="['fas', 'user']"
        ></font-awesome-icon>
        <div
          v-else
          class="icon-image"
          :style="{
            backgroundImage: `url('${formatImageUrl(data.image_url)}')`
          }"
        ></div>
      </div>
    </div>
    <div class="content-container">
      <h3 class="name" v-if="type === SOCIAL_MEDIA.INSTAGRAM">
        {{ data.full_name }}
      </h3>
      <h3 class="name" v-if="type === SOCIAL_MEDIA.FACEBOOK">
        #{{ data.full_name }}
      </h3>

      <div class="info-container">
        <template v-if="type === SOCIAL_MEDIA.INSTAGRAM">
          <div class="item-container">
            <p class="count">{{ data.posts_count }}</p>
            <p class="label">posts</p>
          </div>
          <div class="item-container">
            <p class="count">{{ data.followers }}</p>
            <p class="label">followers</p>
          </div>
          <div class="item-container">
            <p class="count">{{ data.following_count }}</p>
            <p class="label">following</p>
          </div>
        </template>
        <template v-if="type === SOCIAL_MEDIA.FACEBOOK">
          <div class="item-container">
            <p class="count">{{ data.hashtag_posts_count }}</p>
            <p class="label">posts</p>
          </div>
        </template>
      </div>
      <div class="hashtag-container">
        <p
          v-if="type === SOCIAL_MEDIA.INSTAGRAM"
          class="link"
          @click.stop="onLinkClick(data.external_url)"
        >
          #{{ data.external_url }}
        </p>
        <p v-else :style="{ height: '14px' }"></p>
      </div>
    </div>
    <div
      v-if="type === SOCIAL_MEDIA.INSTAGRAM"
      class="media-icon-container"
      @click.stop="onLinkClick(data.account_link)"
    >
      <font-awesome-icon
        class="icon"
        :icon="['fab', 'instagram']"
      ></font-awesome-icon>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed, PropType } from "vue";
import { SOCIAL_MEDIA } from "@/constants/kyc";
import { IgInfoResponse } from "@/typings/kyc";
import { formatImageUrl } from "@/utils";

const Component = defineComponent({
  name: "SocialMediaCard",
  props: {
    type: {
      type: Number,
      default: -1
    },
    data: {
      type: Object as PropType<IgInfoResponse>,
      default: null
    }
  },
  setup(props) {
    const { type } = toRefs(props);

    const socialMediaClass = computed(() => {
      if (type.value < 0) return "";
      return SOCIAL_MEDIA[type.value];
    });

    const onLinkClick = (url: string) => {
      window.open(url, "_blank");
    };

    return {
      socialMediaClass,
      SOCIAL_MEDIA,

      onLinkClick,
      formatImageUrl
    };
  }
});

export default Component;
</script>

